.userPanel {
  width: 100%;
  padding: 22px 20px 0 22px;
}
.userPanel__main {
  display: flex;
  align-items: center;
}
.userPanel__avatar {
  margin-right: 7px;
}
.userPanel__userName,
.userPanel__chevron {
  font-size: 13px;
  line-height: 15px;
  color: #182b4c;
}
.userPanel__chevron {
  display: block;
  margin-left: auto;
  font-weight: 700;
}
