.theNavBar__menu {
  padding: 0 20px;
  background-color: transparent;
  border: none;
}
.theNavBarMenu__item {
  font-size: 13px;
  line-height: 1.15;
  height: auto;
  margin-top: 4px;
  padding: 8px;
  border-radius: 4px;
  color: #182b4c;
  line-height: unset;
}
body.--mobile .theNavBarMenu__item {
  font-size: 14px;
}
.theNavBarMenu__item:first-child {
  margin-top: 22px;
}
.theNavBarMenu__item.is-active {
  color: #0046c5;
  font-weight: 500;
  background-color: #e3e7ec;
}
.theNavBarMenu__item:hover:not(.is-active) {
  color: #182b4c;
  background-color: #e3e7ec;
}
