































































@import "../../node_modules/@x10d/vue-kit/src/styles/mixins/utils.styl"
@import "../../node_modules/@x10d/vue-kit/src/styles/variables/buttons.styl"
@import '../styles/palette.styl'

.section-cnt
  min-height 100vh

.section__aside
  background-color $catskill-white

.section__container
  display flex
  flex-direction column
  height 100%
  max-height 100%
  padding 24px 44px
  background-color $globalColorWhite

  @media(max-width: 1280px)
    padding 0 32px

.section__header
  flex 0 0 auto
  display flex
  align-items center
  height $buttonHeight // Под высоту возможных кнопок-экшнов
  margin-bottom 21px
  padding-bottom 16px
  border-bottom solid 1px $mystic

.sectionHeader__title
  font-size 22px
  line-height 25px

.sectionHeader__addon
  display flex
  margin-left auto

.section__content
  display flex
  flex-direction column
  flex 1 1 0
  overflow auto
