.login {
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background-color: #f2f5f9;
}
.loginForm {
  display: flex;
  flex-direction: column;
  width: 332px;
  margin-top: 30vh;
  padding: 28px 36px 36px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 16px 24px 0 rgba(0,0,0,0.15);
}
.loginForm__title {
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  line-height: 36px;
  color: #182b4c;
}
.loginForm__error {
  display: flex;
  align-items: center;
  margin-top: 17px;
  padding: 13px 16px;
  background-color: #eb171f;
  border-radius: 4px;
}
.loginForm__error svg {
  flex: 0 0 auto;
  margin-right: 10px;
  fill: #fff;
}
.loginForm__errorText {
  font-size: 12px;
  line-height: 16px;
  color: #fff;
}
.loginForm__button {
  display: block;
  margin-top: 15px;
  height: 40px;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.04em;
  font-weight: 500;
  color: #fff;
  background-color: #00daa7;
}
.loginForm__button:disabled {
  color: #b6c0ca;
  background-color: #f2f5f9;
}
.loginForm__button:hover:not(:disabled),
.loginForm__button:focus:not(:disabled) {
  background-color: #04e0a8;
}
.loginForm__link {
  margin: 21px auto 0;
  text-align: center;
  font-size: 13px;
  line-height: 15px;
  color: $globalColorPerano;
  text-decoration: none;
}
/deep/ .baseInput {
  height: 40px;
  padding: 12px 8px;
}
/deep/ .baseInputPassword__changePasswordVisibilityBtn {
  height: 40px;
  width: 40px;
}
/*# sourceMappingURL=src/router-views/Login.css.map */