.section-cnt {
  min-height: 100vh;
}
.section__aside {
  background-color: #f2f5f9;
}
.section__container {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-height: 100%;
  padding: 24px 44px;
  background-color: #fff;
}
@media (max-width: 1280px) {
  .section__container {
    padding: 0 32px;
  }
}
.section__header {
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  height: 32px;
  margin-bottom: 21px;
  padding-bottom: 16px;
  border-bottom: solid 1px #e5e9ef;
}
.sectionHeader__title {
  font-size: 22px;
  line-height: 25px;
}
.sectionHeader__addon {
  display: flex;
  margin-left: auto;
}
.section__content {
  display: flex;
  flex-direction: column;
  flex: 1 1 0;
  overflow: auto;
}
